body {
  color: #ccc;
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

h1 {
  color: white;
  margin: 0;
}

#stage {
  height: 100vh;
  width: 100%;
  background: #262626;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spacer {
  width: 100%;
  height: 100vh;
  background: #5386b2;
  display: flex;
  justify-content: center;
  align-items: center;
}

.box {
  height: 60px;
  width: 60px;
  align-items: center;
  margin: 4px;
  font-size: 1.2em;
  font-weight: 700;
  color: white;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.box1 {
  background-color: #84c186;
}

.box2 {
  background-color: #8b6c4c;
}

.box3 {
  background-color: #39a3ee;
}

.box4 {
  background-color: #ef9144;
}

.box5 {
  background-color: #cd58eb;
}

.box6 {
  background-color: #b84b4b;
}
